@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.module {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}
.module .section__imagebox {
  display: flex;
  justify-content: center;
}
.module .section__imagebox img {
  width: 100%;
  max-width: 505px;
  object-fit: cover;
}
.module .description__button {
  background: #e4c1ae;
  color: white;
  width: 128px;
  height: 48px;
  gap: 10px;
  padding: 16px 40px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  border: none;
  outline: none;
  border-radius: 32px;
}